body {
  font-family: 'Open Sans', sans-serif;
}
.hover-effect {
  content: ' ';
  width: 0;
  border-bottom: 1px solid #000000;
  display: block;
  transition: .5s ease;
  margin: 0 auto;
}
.container {
  margin-top: 20px;
}
.navbar {
  min-height: auto;
}
.navbar-nav {
  text-align: center;
  float: none;
}
.navbar-nav li {
  display: inline-block;
  float: none;
  font-family: 'Open Sans', sans-serif;
}
.navbar-nav li a {
  padding-top: inherit;
  padding-bottom: inherit;
  color: #000000;
  text-transform: uppercase;
}
.navbar-nav li a:hover,
.navbar-nav li a:focus {
  background-color: transparent;
}
.navbar-nav li a:hover:after,
.navbar-nav li a:focus:after {
  width: 100%;
}
.navbar-nav li a:after {
  content: ' ';
  width: 0;
  border-bottom: 1px solid #000000;
  display: block;
  transition: .5s ease;
  margin: 0 auto;
}
.navbar-nav li.active > a:after {
  width: 100%;
}
hr {
  border-top: 1px solid #000000;
}
hr.article-separator {
  width: 30%;
}
.content {
  width: calc(100% - 40px);
  margin: 0 auto;
}
.content h3,
.content h4 {
  font-family: 'Gentium Basic', serif;
  margin: 15px 0;
}
.footer {
  font-family: 'Open Sans', sans-serif;
}
.logo {
  width: 50%;
  max-width: 350px;
  margin: 0 auto;
}
h4 {
  font-size: 22px;
}
article h3 {
  font-size: 20px;
  display: inline-block;
  font-family: 'Open Sans', sans-serif;
}
article h3:after {
  content: ' ';
  width: 0;
  border-bottom: 1px solid #000000;
  display: block;
  transition: .5s ease;
  margin: 0 auto;
}
article a:hover {
  text-decoration: none;
}
article a:hover h3:after {
  width: 100%;
}
a {
  color: #000000;
}
a:hover {
  color: #000000;
}
p {
  font-size: 16px;
}
p.subheading {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 1px;
  font-family: 'Gentium Basic', serif;
}
.home-link {
  margin-top: 30px;
  font-family: 'Open Sans', sans-serif;
}
.home-link a {
  color: #000000;
}
.img-me {
  height: 200px;
  width: auto;
}
.btn {
  border: none;
  font-size: 15px;
  font-weight: normal;
  line-height: 1.4;
  border-radius: 4px;
  padding: 10px 15px;
  -webkit-font-smoothing: subpixel-antialiased;
  transition: border 0.25s linear, color 0.25s linear, background-color 0.25s linear;
}
.portfolio-item > .picture {
  background: #EFF0F2;
  border-radius: 6px;
  padding: 15px;
  margin-bottom: 20px;
  text-align: center;
  opacity: 0.9;
  filter: alpha(opacity=90);
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
}
.portfolio-item a.image {
  height: 180px;
  display: block;
  overflow: hidden;
}
.portfolio-item a.image img {
  max-width: 100%;
}
.portfolio-item .description .btn {
  background-color: #34495e;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  font-weight: 600;
}
.project-images {
  margin-bottom: 15px;
}
.project-images .caption {
  font-family: 'Open Sans', sans-serif;
}
.btn {
  font-family: 'Open Sans', sans-serif;
}
